import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { FormControl } from '@angular/forms';
import { FabricProductModel } from '../../../models/fabric-product.model';
import { GlobalSessionStorageService } from '../../../shared/services/global-session-storage.service';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.scss'],


})
export class SearchComponent implements OnInit, OnChanges {

  selectedIndex = -1;
  @Output() fabricIndex = new EventEmitter();
  allSearched = [];
  @Output() selectedProduct = new EventEmitter();
  @Input() headerTitle: string;
  searchId: FormControl = new FormControl();
  @Input() product;
  @Input() fabricType;
  @Input() fabricSessionType;
  @Input() recommended;
  isShowSearch = false;
  isFabricFound = false;
  isOutOfStock = false;

  constructor() {}

  selectProduct(product) {
    this.allSearched.filter((res) => res.selected = false);
    product.selected = true;
    product.lastSearch = (new Date()).toLocaleString();
    this.selectedIndex = product.id;
    GlobalSessionStorageService.storeDataInSession(this.fabricSessionType, product.id);
    GlobalSessionStorageService.storeDataInSession(`selections.searchFabric'${this.fabricType}`, JSON.stringify(this.product));
    this.selectedProduct.emit(product);
    this.sortFabricsByLatestSearch();
    this.isOutOfStock = false;
  }

  resetSearchText() {
    this.searchId.setValue('');
  }


  searchByFabricId() {
    const fabricId = (this.searchId.value).toLowerCase();
    if (fabricId !== '') {
      // disabled error msg if fabric id found
      this.isFabricFound = false;
      this.isOutOfStock = false;
      let allSearched: FabricProductModel[];
      allSearched = this.allSearched.filter((it) => {
        return (it.id).toLowerCase() === fabricId;
      });
      if (allSearched.length <= 0) {
        allSearched = this.recommended.filter(it => {
          return (it.id).toLowerCase() === fabricId;
        });
        if (allSearched.length > 0) {
          // set all selected fabric false
          this.setAllSelectedFabricsFalse();
          allSearched[0].isSearched = true;
          allSearched[0].lastSearch = (new Date()).toLocaleString();
          allSearched[0].selected = true;
          GlobalSessionStorageService.storeDataInSession(this.fabricSessionType, allSearched[0].id);
          GlobalSessionStorageService.storeDataInSession('selections.factoryCode', allSearched[0].factoryCode);
          this.allSearched.push(allSearched[0]);
          this.selectedProduct.emit(this.allSearched[0]);
          this.sortFabricsByLatestSearch();
        } else {
          this.isFabricFound = true;
        }
      } else {
        this.setAllSelectedFabricsFalse();
        allSearched[0].lastSearch = (new Date()).toLocaleString();
        allSearched[0].selected = true;
        GlobalSessionStorageService.storeDataInSession(this.fabricSessionType, allSearched[0].id);
        GlobalSessionStorageService.storeDataInSession('selections.factoryCode', allSearched[0].factoryCode);
        this.selectedProduct.emit(allSearched[0]);
        this.sortFabricsByLatestSearch();
      }
      GlobalSessionStorageService.storeDataInSession(`selections.searchFabric${this.fabricType}`, JSON.stringify(this.allSearched));
    } else {
      this.isFabricFound = true;
    }
  }

  ngOnInit() {
    if (GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`)) {
      const selectedFabricId = GlobalSessionStorageService.getSessionStorage(this.fabricSessionType);
      this.allSearched = JSON.parse(GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`));
      const fabric = this.allSearched.filter(_ => _.id === selectedFabricId);
      if (fabric[0].isOutOfStock) {
        this.isOutOfStock = true;
        this.selectedIndex = -1;
      } else {
        this.isOutOfStock = false;
      }
      this.sortFabricsByLatestSearch();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    const change = changes['product'];
    if (change) {
      if (!change.firstChange) {
        const curVal = change.currentValue;
        if (curVal) {
          // check if curVal is exist in search History or not
          // if exist skip insertion
          this.setAllSelectedFabricsFalse();
          const allSearched = this.allSearched.filter((it) => it.id === curVal.id);
          if (allSearched.length <= 0) {
            curVal.selected = true;
            curVal.lastSearch = (new Date()).toLocaleString();
            this.allSearched.push(curVal);
            // sort search history by date
            this.sortFabricsByLatestSearch();
          } else {
            allSearched[0].selected = true;
            allSearched[0].lastSearch = (new Date()).toLocaleString();
            // sort search history by date
            this.sortFabricsByLatestSearch();
          }
        }
        GlobalSessionStorageService.storeDataInSession(`selections.searchFabric${this.fabricType}`, JSON.stringify(this.allSearched));
      }
    }
  }

  // sort fabric by latest search
  sortFabricsByLatestSearch() {
    return this.allSearched.sort((date1, date2) => {
      return this.getTime(new Date(date2.lastSearch)) - this.getTime(new Date(date1.lastSearch));
    });
  }

  // set all selected fabrics false
  setAllSelectedFabricsFalse() {
    return this.allSearched.forEach(element => {
      element.selected = false;
    });
  }

  getTime(date?: Date) {
    return date != null ? new Date(date).getTime() : 0;
  }
}

