import { Component, EventEmitter, Input, OnInit, Output, SimpleChanges, OnChanges, ViewChild } from '@angular/core';
import { GlobalSessionStorageService } from '../../../shared/services/global-session-storage.service';
import { FabricProductModel } from '../../../models/fabric-product.model';

@Component({
  selector: 'app-recommended',
  templateUrl: './recommended.component.html',
  styleUrls: ['./recommended.component.scss'],
})
export class RecommendedComponent implements OnInit, OnChanges {
  selectedIndex = -1;
  @Input() recommended;
  @Input() fabricSessionType;
  @Output() selectedProduct = new EventEmitter();
  @Input() fabricType;
  
  slideConfig = {"slidesToShow": 4, "slidesToScroll": 4};
  
  constructor() { }

  ngOnInit() { }

  ngOnChanges(simple: SimpleChanges) {
    const change = simple['recommended'];
    if (change) {
      console.log('change is', change);
      this.recommended = change.currentValue;
      console.log('chnage recommended is', this.recommended);
    }
  }

  selectProduct(product: FabricProductModel) {
    this.selectedIndex = Number(product.id);
    product.selected = true;
    GlobalSessionStorageService.storeDataInSession(this.fabricSessionType, product.id);
    GlobalSessionStorageService.storeDataInSession('selections.factoryCode', product.factoryCode);
    this.selectedProduct.emit(product);
  }
}
