import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { Fabric } from '../models/fabric.model';
import { GlobalSessionStorageService } from '../shared/services/global-session-storage.service';
import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root',
})
export class FabricService {
  baseUrl = '';

  constructor(private http: HttpClient) {
    this.baseUrl = environment.essentialsApiUrl;
  }

  getFabrics(): Observable<Fabric[]> {
    const productLabel = GlobalSessionStorageService.getSessionStorage('selectedBrands');
    const productType = GlobalSessionStorageService.getSessionStorage('selectedGarment');
    const fabricType = 'F';
    const language = 'en';
    const company = 'TMW';
    /* tslint:disable-next-line:max-line-length  --> disabled due to adding extra space in url */
    return this.http.get<Fabric[]>('http://localhost:4200/assets/mock_data/fabric/fabric.json');
    //return this.http.get<Fabric[]>(`${this.baseUrl}fabrics?company=${company}&product_label=${productLabel}&product_type=${productType}&fabric_type=${fabricType}&language=${language}`);
  }

  getLining(): Observable<Fabric[]> {
    const productLabel = GlobalSessionStorageService.getSessionStorage('selectedBrands');
    const productType = GlobalSessionStorageService.getSessionStorage('selectedGarment');
    const fabricType = 'L';
    const language = 'en';
    const company = 'TMW';
    const factoryCode = GlobalSessionStorageService.getSessionStorage('selections.factoryCode');
    /* tslint:disable-next-line:max-line-length */
    return this.http.get<Fabric[]>('http://localhost:4200/assets/mock_data/fabric/fabric.json');
    
    //return this.http.get<Fabric[]>(`${this.baseUrl}fabrics?company=${company}&product_label=${productLabel}&factory_code=${factoryCode}&product_type=${productType}&fabric_type=${fabricType}&language=${language}`);
  }
}
