import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {_} from 'underscore';
// services
import { FilterService } from '../filter.service';
import { NavigationService } from '../../shared/services/navigation.service';
// models
import { FabricProductModel } from '../../models/fabric-product.model';
import { BreadCrumb } from '../../models/breadcrumb.model';
import { Sidebar } from '../../models/sidebar.model';

@Component({
  selector: 'app-filter',
  templateUrl: './filter.component.html',
})
export class FilterComponent implements OnInit {
  recommended: FabricProductModel[] = [];
  filtered = [];
  breadcrumbData: BreadCrumb[] = [];
  sideMenuData: Sidebar[] = [];
  // get real data from fabricColorGroups
  filterColors = [];
  // get real data from filterPatternGroups
  filterPattern = [];

  filters = {
    fabricColor: [],
    pattern: [],
  };

  constructor(private filterService: FilterService,
              private _location: Location,
              private navigationService: NavigationService) { }

  ngOnInit() {
    debugger;
    const allFabrics = this.filterService.allFabrics;
    this.recommended = allFabrics['collection'];
    this.filterColors = allFabrics['colorGroups'];
    this.filterPattern = allFabrics['patternGroups'];
    this.navigationService.getBreadcrumbData().subscribe(
      (it) => {
        this.breadcrumbData = it.Fabric;
      });
  }

  filterByColor(color) {
    const isFilter = false;
    if (color.isFilter) {
      color.isFilter = isFilter;
      this.filters.fabricColor = this.filters.fabricColor.filter((res) => res.id !== color.id);
    } else {
      color.isFilter = !isFilter;
      this.filters.fabricColor.push(color);
    }
    this.filtered = this.multiFilter(this.recommended, this.filters);
  }

  filterByPattern(pattern) {
    const isFilter = false;
    if (pattern.isFilter) {
      pattern.isFilter = isFilter;
      this.filters.pattern = this.filters.pattern.filter((res) => res.patternGroupCode !== pattern.patternGroupCode);
    } else {
      pattern.isFilter = !isFilter;
      this.filters.pattern.push(pattern);
    }
    this.filtered = this.multiFilter(this.recommended, this.filters);
  }
  // apply filter on basis of filter condition
  multiFilter(array, filter) {
    const filters = _.clone(filter);
    // delete pattern and color from object if length is 0
    // before filters
    console.log('filter is', filter);
    if (filters.fabricColor.length === 0) {
      delete filters.fabricColor;
    }
    if (filters.pattern.length === 0) {
      delete filters.pattern;
    }
    
    if (filters.pattern) {
          array = array.filter(
            function (el) {
              let found = false;
              filters.pattern.forEach(function (patternFilter, index) {
                if (el.patternGroupCode === patternFilter.patternGroupCode) {
                  found = true;
                }
              });

              return found;
            }
          );
        }

    if (filters.fabricColor) {
          array = array.filter(
            function (el) {
              let found = false;
              filters.fabricColor.forEach(function (fabricColorFilter, index) {
                el.fabricColorGroupCodes.forEach(function (fabricColor, index) {
                  if (fabricColor === fabricColorFilter.id) {
                    found = true;
                  }
                })
              })
              return found;
            }
          );
        }
      console.log(array);
      return array;  
  }
  // This method will fire when user click on Done button.
  appliedFilter() {
    this.filterService.filterdFabrics = this.filtered;
    this.filterService.appliedFiltersCount = this.countAllFilters();
    this._location.back();
  }
  // Count all filters applied on colors and pattern
  countAllFilters() {
    const countColorFilters = this.filters.fabricColor.length;
    const countPatternFilters = this.filters.pattern.length;
    const totalFilters = countColorFilters + countPatternFilters;
    return totalFilters;
  }
}
