import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// services
import { GlobalSessionStorageService } from '../../shared/services/global-session-storage.service';
import { NavigationService } from '../../shared/services/navigation.service';
import { FabricService } from '../fabric.service';
import { FilterService } from '../filter.service';
// models
import { FabricProductModel } from '../../models/fabric-product.model';
import { BreadCrumb } from '../../models/breadcrumb.model';
import { Sidebar } from '../../models/sidebar.model';
import { Fabric } from '../../models/fabric.model';

@Component({
  selector: 'app-vest-lining',
  templateUrl: './lining-default.component.html',
})
export class LiningDefaultComponent implements OnInit {
  headerTitle = '';
  selectedIndex = 0;
  fabricSessionType = '';
  recommended: FabricProductModel[] = [];
  breadcrumbData: BreadCrumb[] = [];
  sideMenuData: Sidebar[] = [];
  fabricType = '';
  title: string;
  url: string;
  isOpen = false;
  product: FabricProductModel;
  isOutOfStock = false;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private fabricService: FabricService,
    private navigationService: NavigationService,
    private filterService: FilterService,
  ) { }

  ngOnInit() {
    // get all snapshot parameter
    const data = this.route.snapshot.data;
    this.headerTitle = data.title;
    this.fabricType = data.type;
    this.fabricSessionType = data.sessionType;
    this.navigationService.getBreadcrumbData().subscribe(
      (it) => {
        this.breadcrumbData = it.Fabric;
      });
    this.getLiningDefaultData();
  }
  getOutOfStockData() {
    if (GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`)) {
      const allSearched = JSON.parse(GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`));
      if (allSearched.length > 0) {
        for (let i = 0; i < allSearched.length; i++) {
          for (let j = 0; j < this.recommended.length; j++) {
            if (allSearched[i] === this.recommended[j].id) {
              allSearched[i].isOutOfStock = false;
            } else {
              allSearched[i].isOutOfStock = true;
            }
          }
        }
      }
      GlobalSessionStorageService.storeDataInSession(`selections.searchFabric${this.fabricType}`, JSON.stringify(allSearched));
    }
  }

  goToFilterPage() {
    this.filterService.allFabrics = this.recommended;
    this.router.navigate(['./fabric/filter']);
  }

  getSelectedIndex(event) {
    this.selectedIndex = event.id;
    this.product = event;
  }

  getLiningDefaultData() {
    this.fabricService.getFabrics()
      .subscribe((res: Fabric[]) => {
        this.recommended = res['collection'];
        this.recommended.filter((_) => _.isOutOfStock = false);
        if (GlobalSessionStorageService.getSessionStorage(this.fabricSessionType)) {
          this.selectedIndex = this.recommended.findIndex(item => {
            return item.id === GlobalSessionStorageService.getSessionStorage(this.fabricSessionType);
          });
          this.product = this.recommended[this.selectedIndex];
          this.getOutOfStockData();
          this.disableNextButton();
        } else {
          this.product = this.recommended[this.selectedIndex];
          this.product.selected = true;
          GlobalSessionStorageService.storeDataInSession(`selections.searchFabric${this.fabricType}`, JSON.stringify(this.product));
          GlobalSessionStorageService.storeDataInSession(this.fabricSessionType, this.product.id);
        }
      });
  }

  toggle() {
    this.isOpen = !this.isOpen;
  }

  // disabled next button if selected fabric is not available
  disableNextButton() {
    if (GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`)) {
      const selectedFabricId = GlobalSessionStorageService.getSessionStorage(this.fabricSessionType);
      const allFabrics = JSON.parse(GlobalSessionStorageService.getSessionStorage(`selections.searchFabric${this.fabricType}`));
      if (allFabrics.length > 0) {
        const fabric = allFabrics.filter(_ => _.id === selectedFabricId);
        if (fabric[0].isOutOfStock) {
          this.selectedIndex = -1;
        } else {
          this.selectedIndex = -1;
        }
      }
    }
  }
}
